import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data, page, search, sort } = props.table;
        let [name, setName] = useState(selected!==-1?data[selected][1]:'');
        let handleName = (event) => {
            setName(event.target.value)
        };
        let [genre, setGenre] = useState(selected!==-1?data[selected][2]:'');
        let handleGenre = (event) => {
            setGenre(event.target.value)
        };
        let types = ['скоро', 'в прокате']
        let [type, setType] = useState(selected!==-1?data[selected][3]:'');
        let handleType = (event) => {
            setType(event.target.value)
        };
        let [duration, setDuration] = useState(selected!==-1?data[selected][4]:'');
        let handleDuration = (event) => {
            setDuration(event.target.value)
        };
        let [ageCategory, setAgeCategory] = useState(selected!==-1?data[selected][5]:'');
        let handleAgeCategory = (event) => {
            setAgeCategory(event.target.value)
        };
        let [premier, setPremier] = useState(selected!==-1?data[selected][6]:'');
        let handlePremier = (event) => {
            setPremier(event.target.value)
        };
        let [producers, setPoducers] = useState(selected!==-1?data[selected][7]:'');
        let handleProducers = (event) => {
            setPoducers(event.target.value)
        };
        let [actors, setActors] = useState(selected!==-1?data[selected][8]:'');
        let handleActors = (event) => {
            setActors(event.target.value)
        };
        let [description, setDescription] = useState(selected!==-1?data[selected][9]:'');
        let handleDescription = (event) => {
            setDescription(event.target.value)
        };
        let [file, setFile] = useState([]);
        let [fileNames, setFileNames] = useState(selected!==-1?data[selected][0]:'');
        let handleChangeFile = ((event) => {
            setFile(event.target.files)
            let fileNames='';
            for(let i=0; i<event.target.files.length; i++){
                if(i!==0)
                    fileNames+=', '
                fileNames+=event.target.files[i].name+','
            }
            setFileNames(fileNames)
        })
        const { classes } = props;
        return (
            <div>
                <TextField
                    label='имя'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={name}
                    onChange={handleName}
                />
                <br/>
                <TextField
                    label='жанр'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={genre}
                    onChange={handleGenre}
                />
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='тип'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={type}
                    onChange={handleType}
                >
                    {types != undefined?
                        types.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                        :
                        null
                    }
                </TextField>
                <br/>
                <TextField
                    label='продолжительность'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={duration}
                    onChange={handleDuration}
                />
                <br/>
                <TextField
                    label='возраст'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={ageCategory}
                    onChange={handleAgeCategory}
                />
                <br/>
                <TextField
                    label='премьера'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={premier}
                    onChange={handlePremier}
                />
                <br/>
                <TextField
                    label='актеры'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={actors}
                    onChange={handleActors}
                />
                <br/>
                <TextField
                    label='режиссеры'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={producers}
                    onChange={handleProducers}
                />
                <br/>
                <TextField
                    multiline
                    rowsMax='4'
                    label='описание'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={description}
                    onChange={handleDescription}
                />
                <br/>
                <div className={classes.urls}>
                    {fileNames}
                </div>
                <br/>
                <label htmlFor='contained-button-file'>
                    <Button variant='contained' color={fileNames.length>0?'primary':''} component='span' className={classes.button}>
                        Загрузить изображение
                    </Button>
                </label>
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                        if(selected===-1)
                            addData({search: search, sort: sort, page: page, name: 'Кино', file: file, data: {name: name, genre: genre, type: type, description: description, duration: duration, ageCategory: ageCategory, premier: premier,  producers: producers, actors: actors}});
                        else
                            setData({id: data[selected][11], search: search, sort: sort, page: page, name: 'Кино', oldFile: data[selected][0], file: file, data: {name: name, genre: genre, type: type, description: description, duration: duration, ageCategory: ageCategory, premier: premier,  producers: producers, actors: actors}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
                <input
                    accept='image/*'
                    style={{ display: 'none' }}
                    id='contained-button-file'
                    type='file'
                    onChange={handleChangeFile}
                />
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));