import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data, page, search, sort } = props.table;

        let [coords, setСoords] = useState(selected!==-1?data[selected][0]:'');
        let handleCoords =  (event) => {
            setСoords(event.target.value)
        };
        let [cashbox, setСashbox] = useState(selected!==-1?data[selected][1]:'');
        let handleCashbox =  (event) => {
            setСashbox(event.target.value)
        };
        let [address, setAddress] = useState(selected!==-1?data[selected][2]:'');
        let handleAddress =  (event) => {
            setAddress(event.target.value)
        };
        let [booking, setBooking] = useState(selected!==-1?data[selected][3]:'');
        let handleBooking =  (event) => {
            setBooking(event.target.value)
        };
        let [connection, setConnection] = useState(selected!==-1?data[selected][4]:'');
        let handleConnection =  (event) => {
            setConnection(event.target.value)
        };
        let [return1, setReturn1] = useState(selected!==-1?data[selected][5]:'');
        let handleReturn1 =  (event) => {
            setReturn1(event.target.value)
        };
        let [general, setGeneral] = useState(selected!==-1?data[selected][6]:'');
        let handleGeneral =  (event) => {
            setGeneral(event.target.value)
        };
        let [cooperation, setCooperation] = useState(selected!==-1?data[selected][7]:'');
        let handleCooperation =  (event) => {
            setCooperation(event.target.value)
        };
        const { classes } = props;
        return (
            <div>
                <TextField
                    label='координаты'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={coords}
                    onChange={handleCoords}
                />
                <br/>
                <TextField
                    label='касса'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={cashbox}
                    onChange={handleCashbox}
                />
                <br/>
                <TextField
                    label='адрес'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={address}
                    onChange={handleAddress}
                />
                <br/>
                <TextField
                    label='бронирование'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={booking}
                    onChange={handleBooking}
                />
                <br/>
                <TextField
                    label='связь'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={connection}
                    onChange={handleConnection}
                />
                <br/>
                <TextField
                    label='возврат'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={return1}
                    onChange={handleReturn1}
                />
                <br/>
                <TextField
                    label='главная'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={general}
                    onChange={handleGeneral}
                />
                <br/>
                <TextField
                    label='сотрудничество'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={cooperation}
                    onChange={handleCooperation}
                />
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                        if(selected===-1)
                            addData({search: search, sort: sort, page: page, name: 'Контакт', data: {coords: coords, cashbox: cashbox, address: address, booking: booking, connection: connection, return1: return1, general: general, cooperation: cooperation}});
                        else
                            setData({id: data[selected][9], search: search, sort: sort, page: page, name: 'Контакт', data: {coords: coords, cashbox: cashbox, address: address, booking: booking, connection: connection, return1: return1, general: general, cooperation: cooperation}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));