import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data, page, search, sort } = props.table;

        let [nameRu, setNameRu] = useState(selected!==-1?data[selected][0]:'');
        let handleNameRu =  (event) => {
            setNameRu(event.target.value)
        };
        let [requirementsRu, setRequirementsRu] = useState(selected!==-1?data[selected][1]:'');
        let handleRequirementsRu =  (event) => {
            setRequirementsRu(event.target.value)
        };
        let [dutiesRu, setDutiesRu] = useState(selected!==-1?data[selected][2]:'');
        let handleDutiesRu =  (event) => {
            setDutiesRu(event.target.value)
        };
        let [conditionsRu, setConditionsRu] = useState(selected!==-1?data[selected][3]:'');
        let handleConditionsRu =  (event) => {
            setConditionsRu(event.target.value)
        };
        let [nameKg, setNameKg] = useState(selected!==-1?data[selected][4]:'');
        let handleNameKg =  (event) => {
            setNameKg(event.target.value)
        };
        let [requirementsKg, setRequirementsKg] = useState(selected!==-1?data[selected][5]:'');
        let handleRequirementsKg =  (event) => {
            setRequirementsKg(event.target.value)
        };
        let [dutiesKg, setDutiesKg] = useState(selected!==-1?data[selected][6]:'');
        let handleDutiesKg =  (event) => {
            setDutiesKg(event.target.value)
        };
        let [conditionsKg, setConditionsKg] = useState(selected!==-1?data[selected][7]:'');
        let handleConditionsKg =  (event) => {
            setConditionsKg(event.target.value)
        };
        const { classes } = props;
        return (
            <div>
                <TextField
                    label='название'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={nameRu}
                    onChange={handleNameRu}
                />
                <br/>
                <div className={classes.urls}>
                    Для переноса на новую строку вставьте символ: <b>|</b>
                </div>
                <TextField
                    label='обязанности'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={requirementsRu}
                    onChange={handleRequirementsRu}
                />
                <br/>
                <div className={classes.urls}>
                    Для переноса на новую строку вставьте символ: <b>|</b>
                </div>
                <TextField
                    label='требования'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={dutiesRu}
                    onChange={handleDutiesRu}
                />
                <br/>
                <div className={classes.urls}>
                    Для переноса на новую строку вставьте символ: <b>|</b>
                </div>
                <TextField
                    label='условия'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={conditionsRu}
                    onChange={handleConditionsRu}
                />
                <br/>
                <TextField
                    label='ысым'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={nameKg}
                    onChange={handleNameKg}
                />
                <br/>
                <div className={classes.urls}>
                    Для переноса на новую строку вставьте символ: <b>|</b>
                </div>
                <TextField
                    label='милдеттери'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={requirementsKg}
                    onChange={handleRequirementsKg}
                />
                <br/>
                <div className={classes.urls}>
                    Для переноса на новую строку вставьте символ: <b>|</b>
                </div>
                <TextField
                    label='талаптар'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={dutiesKg}
                    onChange={handleDutiesKg}
                />
                <br/>
                <div className={classes.urls}>
                    Для переноса на новую строку вставьте символ: <b>|</b>
                </div>
                <TextField
                    label='шарттары'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={conditionsKg}
                    onChange={handleConditionsKg}
                />
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                        if(selected===-1)
                            addData({search: search, sort: sort, page: page, name: 'Вакансии', data: {nameRu: nameRu, requirementsRu: requirementsRu, dutiesRu: dutiesRu, conditionsRu: conditionsRu, nameKg: nameKg, requirementsKg: requirementsKg, dutiesKg: dutiesKg,  conditionsKg: conditionsKg}});
                        else
                            setData({id: data[selected][9], search: search, sort: sort, page: page, name: 'Вакансии', data: {nameRu: nameRu, requirementsRu: requirementsRu, dutiesRu: dutiesRu, conditionsRu: conditionsRu, nameKg: nameKg, requirementsKg: requirementsKg, dutiesKg: dutiesKg,  conditionsKg: conditionsKg}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));