import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data, page, search, sort } = props.table;

        let [nameRu, setNameRu] = useState(selected!==-1?data[selected][1]:'');
        let handleNameRu =  (event) => {
            setNameRu(event.target.value)
        };
        let [nameKg, setNameKg] = useState(selected!==-1?data[selected][2]:'');
        let handleNameKg =  (event) => {
            setNameKg(event.target.value)
        };
        let [address, setAddress] = useState(selected!==-1?data[selected][3]:'');
        let handleAddress = (event) => {
            setAddress(event.target.value)
        };
        let [city, setCity] = useState(selected!==-1?data[selected][4]:'');
        let handleCity = (event) => {
            setCity(event.target.value)
        };
        let [coords, setCoords] = useState(selected!==-1?data[selected][5]:'');
        let handleCoords =  (event) => {
            setCoords(event.target.value)
        };
        let [file, setFile] = useState([]);
        let [fileNames, setFileNames] = useState(selected!==-1?data[selected][0]:'');
        let handleChangeFile = ((event) => {
            setFile(event.target.files)
            let fileNames='';
            for(let i=0; i<event.target.files.length; i++){
                if(i!==0)
                    fileNames+=', '
                fileNames+=event.target.files[i].name+','
            }
            setFileNames(fileNames)
        })
        const { classes } = props;
        return (
            <div>
                <TextField
                    label='название'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={nameRu}
                    onChange={handleNameRu}
                />
                <br/>
                <TextField
                    label='ысым'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={nameKg}
                    onChange={handleNameKg}
                />
                <br/>
                <TextField
                    label='адрес'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={address}
                    onChange={handleAddress}
                />
                <br/>
                <TextField
                    label='город'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={city}
                    onChange={handleCity}
                />
                <br/>
                <TextField
                    label='координаты'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={coords}
                    onChange={handleCoords}
                />
                <br/>
                <div className={classes.urls}>
                    {fileNames}
                </div>
                <br/>
                <label htmlFor='contained-button-file'>
                    <Button variant='contained' color={fileNames.length>0?'primary':''} component='span' className={classes.button}>
                        Загрузить изображение
                    </Button>
                </label>
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                        if(selected===-1)
                            addData({search: search, sort: sort, page: page, name: 'Площадка', file: file, data: {city: city, nameRu: nameRu, nameKg: nameKg, address: address, coords: coords}});
                        else
                            setData({id: data[selected][7], search: search, sort: sort, page: page, name: 'Площадка', oldFile: data[selected][0], file: file, data: {city: city, nameRu: nameRu, nameKg: nameKg, address: address, coords: coords}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
                <input
                    accept='image/*'
                    style={{ display: 'none' }}
                    id='contained-button-file'
                    type='file'
                    onChange={handleChangeFile}
                />
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));