import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data, page, search, sort } = props.table;

        let [questionRu, setQuestionRu] = useState(selected!==-1?data[selected][0]:'');
        let handleQuestionRu =  (event) => {
            setQuestionRu(event.target.value)
        };
        let [answerRu, setAnswerRu] = useState(selected!==-1?data[selected][1]:'');
        let handleAnswerRu =  (event) => {
            setAnswerRu(event.target.value)
        };
        let [questionKg, setQuestionKg] = useState(selected!==-1?data[selected][2]:'');
        let handleQuestionKg =  (event) => {
            setQuestionKg(event.target.value)
        };
        let [answerKg, setAnswerKg] = useState(selected!==-1?data[selected][3]:'');
        let handleAnswerKg =  (event) => {
            setAnswerKg(event.target.value)
        };
        const { classes } = props;
        return (
            <div>
                <TextField
                    label='вопрос'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={questionRu}
                    onChange={handleQuestionRu}
                />
                <br/>
                <TextField
                    label='ответ'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={answerRu}
                    onChange={handleAnswerRu}
                />
                <br/>
                <TextField
                    label='суроо'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={questionKg}
                    onChange={handleQuestionKg}
                />
                <br/>
                <TextField
                    label='жооп'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={answerKg}
                    onChange={handleAnswerKg}
                />
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                        if(selected===-1)
                            addData({search: search, sort: sort, page: page, name: 'FAQ', data: { questionRu: questionRu, answerRu: answerRu, questionKg: questionKg, answerKg: answerKg}});
                        else
                            setData({id: data[selected][5], search: search, sort: sort, page: page, name: 'FAQ', data: { questionRu: questionRu, answerRu: answerRu, questionKg: questionKg, answerKg: answerKg}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));