import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    text_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        overflow: 'hidden',
        wordWrap: 'break-word'
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
    menu: {
        width: 200,
    }
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data, page, search, sort } = props.table;

        let [name, setName] = useState(selected!==-1?data[selected][0]:'');
        let handleName =  (event) => {
            setName(event.target.value)
        };
        let [surname, setSurame] = useState(selected!==-1?data[selected][1]:'');
        let handleSurname =  (event) => {
            setSurame(event.target.value)
        };
        let [email, setEmail] = useState(selected!==-1?data[selected][2]:'');
        let handleEmail =  (event) => {
            setEmail(event.target.value)
        };
        let [phonenumber, setPhonenumber] = useState(selected!==-1?data[selected][3]:'');
        let handlePhonenumber = (event) => {
            setPhonenumber(event.target.value)
        };
        let roles = ['manager', 'accountant', 'cashier', 'client', 'cinema', 'turnstile', 'organizator']
        let [role, setRole] = useState(selected!==-1?data[selected][4]:'');
        let handleRole = (event) => {
            setRole(event.target.value)
        };
        let statuses = ['active', 'inactive']
        let [status, setStatus] = useState(selected!==-1?data[selected][5]:'');
        let handleStatus =  (event) => {
            setStatus(event.target.value)
        };
        let [password, setPassword] = useState('');
        let handlePassword =  (event) => {
            setPassword(event.target.value)
        };
        const { classes } = props;
        return (
            <div>
                <TextField
                    label='имя'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={name}
                    onChange={handleName}
                />
                <br/>
                <TextField
                    label='фамилия'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={surname}
                    onChange={handleSurname}
                />
                <br/>
                {selected!==-1?
                    <>
                    <br/>
                    <div className={classes.textField}>
                        <b>email:</b> {email}
                    </div>
                    <br/>
                    </>
                    :
                    <>
                    <TextField
                        label='email'
                        type='login'
                        className={classes.textField}
                        margin='normal'
                        value={email}
                        onChange={handleEmail}
                    />
                    <br/>
                    </>
                }
                <TextField
                    label='телефон'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={phonenumber}
                    onChange={handlePhonenumber}
                />
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='роль'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={role}
                    onChange={handleRole}
                >
                    {roles != undefined?
                        roles.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                        :
                        null
                    }
                </TextField>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='статус'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={status}
                    onChange={handleStatus}
                >
                    {statuses != undefined?
                        statuses.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                        :
                        null
                    }
                </TextField>
                <br/>
                <br/>
                    <TextField
                        label='пароль'
                        type='login'
                        className={classes.textField}
                        margin='normal'
                        value={password}
                        onChange={handlePassword}
                    />
                    <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                        if(selected===-1)
                            addData({search: search, sort: sort, page: page, name: 'Пользователи', data: {email: email, name: name, surname: surname, phonenumber: phonenumber, role: role, password: password, status: status}});
                        else
                            setData({id: data[selected][7], search: search, sort: sort, page: page, name: 'Пользователи', data: {email: email, name: name, surname: surname, phonenumber: phonenumber, role: role,  password: password, status: status}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));