import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
    text_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        overflow: 'hidden',
        wordWrap: 'break-word'
    },
    menu: {
        width: 200,
    }
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data, page, search, sort } = props.table;
        let wallet = selected!==-1?data[selected][0]:'';
        let ticket = selected!==-1?data[selected][1]:'';
        let service = selected!==-1?data[selected][2]:'';
        let sum = selected!==-1?data[selected][3]:'';
        let meta = selected!==-1?data[selected][4]:'';
        let [status, setStatus] = useState(selected!==-1?data[selected][5]:'');
        let name = selected!==-1?data[selected][6]:'';
        let email = selected!==-1?data[selected][7]:'';
        let sms = selected!==-1?data[selected][8]:'';
        let statuses = ['обработка', 'совершен', 'отменен', 'ошибка']
        let handleStatus =  (event) => {
            setStatus(event.target.value)
        };

        const { classes } = props;
        return (
            <div className={classes.text_message}>
                <b>Адрес</b>
                <br/>
                {wallet}
                <br/>
                <br/>
                <b>Билет</b>
                <br/>
                {ticket}
                <br/>
                <br/>
                <b>Сумма</b>
                <br/>
                {sum}
                <br/>
                <br/>
                <b>Сервис</b>
                <br/>
                {service}
                <br/>
                <br/>
                <b>Meta</b>
                <br/>
                {meta}
                <br/>
                <br/>
                <b>Имя</b>
                <br/>
                {name}
                <br/>
                <br/>
                <b>Email</b>
                <br/>
                {email}
                <br/>
                <br/>
                <b>SMS</b>
                <br/>
                {sms}
                <br/>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='статус'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={status}
                    onChange={handleStatus}
                >
                    {statuses != undefined?
                        statuses.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                        :
                        null
                    }
                </TextField>
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                        if(selected===-1)
                            addData({search: search, sort: sort, page: page, name: 'Платежи', data: {status: status}});
                        else
                            setData({id: data[selected][10], search: search, sort: sort, page: page, name: 'Платежи', data: {status: status}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));