import { SET_MINI_DIALOG, SHOW_MINI_DIALOG } from '../constants/mini_dialog'
import React from 'react'
import {
    store
} from '../../index'
import About from '../../component/Add/About';
import Email from '../../component/Add/Email';
import Ticket from '../../component/Add/Ticket';
import Contact from '../../component/Add/Contact';
import Ads from '../../component/Add/Ads';
import Faq from '../../component/Add/Faq';
import Logo from '../../component/Add/Logo';
import Personal from '../../component/Add/Personal';
import Phone from '../../component/Add/Phone';
import Social from '../../component/Add/Social';
import Vacancy from '../../component/Add/Vacancy';
import Where from '../../component/Add/Where';
import User from '../../component/Add/User';
import Billboard from '../../component/Add/Billboard';
import Cinema from '../../component/Add/Cinema';
import Mailing from '../../component/Add/Mailing';
import Hall from '../../component/Add/Hall';
import TicketCinema from '../../component/Add/TicketCinema';
import Balance from '../../component/Add/Balance';
import Cashbox from '../../component/Add/Cashbox';
import Payment from '../../component/Add/Payment';

export function setMiniDialog(title,child) {
    console.log('setMD')
    return {
        type: SET_MINI_DIALOG,
        payload: {title: title, child: child}
    }
}

export function showAddMiniDialog() {
    console.log('showadd')
    return async (dispatch) => {
        if(store.getState().table.name!=''){
            let child;
            if(store.getState().table.name=='Рассылка')
                child = <Mailing/>
            else if(store.getState().table.name=='Билеты кино')
                child = <TicketCinema/>
            else if(store.getState().table.name=='О нас')
                child = <About/>
            else if(store.getState().table.name=='Билеты')
                child = <Ticket/>
            else if(store.getState().table.name=='Кино')
                child = <Cinema/>
            else if(store.getState().table.name=='Email')
                child = <Email/>
            else if(store.getState().table.name=='Площадка')
                child = <Where/>
            else if(store.getState().table.name=='Вакансии')
                child = <Vacancy/>
            else if(store.getState().table.name=='Реклама')
                child = <Ads/>
            else if(store.getState().table.name=='Контакт')
                child = <Contact/>
            else if(store.getState().table.name=='FAQ')
                child = <Faq/>
            else if(store.getState().table.name=='Логотип')
                child = <Logo/>
            else if(store.getState().table.name=='Персонал')
                child = <Personal/>
            else if(store.getState().table.name=='Телефон')
                child = <Phone/>
            else if(store.getState().table.name=='Социалки')
                child = <Social/>
            else if(store.getState().table.name=='Пользователи')
                child = <User/>
            else if(store.getState().table.name=='Биллборды')
                child = <Billboard/>
            else if(store.getState().table.name=='Залы')
                child = <Hall/>
            else if(store.getState().table.name=='Баланс')
                child = <Balance/>
            else if(store.getState().table.name=='Кассы')
                child = <Cashbox/>
            else if(store.getState().table.name=='Платежи')
                child = <Payment/>
            dispatch ({
                type: SET_MINI_DIALOG,
                payload: {title: store.getState().table.name, child: child}
            })
            dispatch ({
                type: SHOW_MINI_DIALOG,
                payload: true
            })
        }
    }
}

export function showMiniDialog(show) {
    console.log('showmini')
    return {
        type: SHOW_MINI_DIALOG,
        payload: show
    }
}
