import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import { getDataSimple } from '../../redux/actions/table'
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data, page, search, sort } = props.table;

        useEffect(()=>{(async()=>{
            await setEvents(await getDataSimple({name: 'СобытиеID'}));
        })()},[])
        let [events, setEvents] = useState([]);
        let [event, setEvent] = useState(selected!==-1?data[selected][2].split('\n')[1]:'');
        let handleEvent =  (event) => {
            setEvent(event.target.value)
        };
        let [name, setName] = useState(selected!==-1?data[selected][1]:'');
        let handleName =  (event) => {
            setName(event.target.value)
        };
        let dateStart1;
        if(selected!==-1) {
            dateStart1 = {part1: data[selected][3].split(' ')};
            dateStart1.part2 = dateStart1.part1[1];
            dateStart1.part1 = dateStart1.part1[0].split('.');
            dateStart1 =  dateStart1.part1[0]+'-'+dateStart1.part1[2]+'-'+dateStart1.part1[1]+'T'+dateStart1.part2+':00';
        }
        let [dateStart, setDateStart] = useState(selected!==-1?dateStart1:'2019-01-01T00:00');
        let handleDateStart =  (event) => {
            setDateStart(event.target.value)
        };
        let dateEnd1;
        if(selected!==-1){
            dateEnd1 = {part1: data[selected][4].split(' ')};
            dateEnd1.part2 = dateEnd1.part1[1];
            dateEnd1.part1 = dateEnd1.part1[0].split('.');
            dateEnd1 =  dateEnd1.part1[0]+'-'+dateEnd1.part1[2]+'-'+dateEnd1.part1[1]+'T'+dateEnd1.part2+':00';
        }
        console.log(dateEnd1, dateStart1)
        let [dateEnd, setDateEnd] = useState(selected!==-1?dateEnd1:'2019-01-01T00:00');
        let handleDateEnd =  (event) => {
            setDateEnd(event.target.value)
        };
        let [file, setFile] = useState([]);
        let [fileNames, setFileNames] = useState(selected!==-1?data[selected][0]:'');
        let handleChangeFile = ((event) => {
            setFile(event.target.files)
            let fileNames='';
            for(let i=0; i<event.target.files.length; i++){
                if(i!==0)
                    fileNames+=', '
                fileNames+=event.target.files[i].name+','
            }
            setFileNames(fileNames)
        })
        const { classes } = props;
        return (
            <div>
                <TextField
                    label='имя'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={name}
                    onChange={handleName}
                />
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='событие'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={event}
                    onChange={handleEvent}
                >
                    {events != undefined?
                        events.map(option => {
                            return(
                            <MenuItem key={option._id} value={option._id}>
                                {option.nameRu}
                            </MenuItem>)
                        })
                        :
                        null
                    }
                </TextField>
                <br/>
                <TextField
                    label='дата начала'
                    type='datetime-local'
                    className={classes.textField}
                    margin='normal'
                    value={dateStart}
                    onChange={handleDateStart}
                />
                <br/>
                <TextField
                    label='дата окончания'
                    type='datetime-local'
                    className={classes.textField}
                    margin='normal'
                    value={dateEnd}
                    onChange={handleDateEnd}
                />
                <br/>
                <div className={classes.urls}>
                    {fileNames}
                </div>
                <br/>
                <label htmlFor='contained-button-file'>
                    <Button variant='contained' color={fileNames.length>0?'primary':''} component='span' className={classes.button}>
                        Загрузить изображение
                    </Button>
                </label>
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                        if(selected===-1)
                            addData({search: search, sort: sort, page: page, name: 'Биллборды', file: file, data: {name: name, dateStart: dateStart, dateEnd: dateEnd, event: event}});
                        else
                            setData({id: data[selected][6], search: search, sort: sort, page: page, name: 'Биллборды', oldFile: data[selected][0], file: file, data: {name: name, dateStart: dateStart, dateEnd: dateEnd, event: event}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
                <input
                    accept='image/*'
                    style={{ display: 'none' }}
                    id='contained-button-file'
                    type='file'
                    onChange={handleChangeFile}
                />
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));